import React, { Component } from 'react';

class SearchTitle extends Component {

  handleMenu = (e) => {
    const genre = document.getElementById("searchGenresList")
    genre.classList.toggle("active")
  }

  render() {

  return (
    <div className="SearchTitle" onClick={this.handleMenu} >
          <h2 className="SearchTitle-text">
            {this.props.title}
          </h2>

          <div className="searchGenresMenu">
			<img src="/static/img/arrowUp.svg" alt=""/>
		  </div>

          <style jsx>{`
			.SearchTitle {
				position: relative;
				padding: 10px;
				cursor: pointer;
				transition: .25s linear;
			}

			.SearchTitle:hover .searchGenresMenu{
				transform: rotate(90deg)
			}

			.SearchTitle-text {
				text-align: left;
				font-size: 18px;
				font-weight: bold;
				margin-bottom: 15px;
				margin-top: 10px;
				margin-left: 10px;
				text-align: left;
			}

			.searchGenresMenu {
			  position: absolute;
			  top: 23px;
			  right: 20px;
			  cursor: pointer;
			}

			.searchGenresMenu img {
			  width: 15px;
			}

		`}</style>
    </div>
  );
  }
}

export default SearchTitle;
